import "./store.scss";
import { Link } from "react-router-dom";
import Section_Tag from "../../components/Section_Tag";

export default function Reseve() {
  return (
    <div className="reserve">
      <div className="reserve_title">
        <Section_Tag/><h2>스타벅스 리저브 매장</h2>
      </div>
      <div className="reserve_part1">
        <img
          src="../../image/reserve/221214_reserve_store_top_img.png"
          alt="reserve_top"
        />
        <div className="reserve_part1_text">
          <h3>WHERE MY TASTE STAYS</h3>
          <p>
            나의 취향이 머무는 곳, STARBUCKS RESERVE™ 매장 입니다.
            <br />
            취향을 채우는 스페셜티 커피와 깊은 풍미를 더하는 추출 방식까지.
            <br />
            커피향으로 가득 채워진 스타벅스 리저브 매장에서 모두 경험해 보세요.
          </p>
        </div>
      </div>
      <div className="reserve_part2">
        <div className="reserve_part2_text">
          <img
            src="../../image/reserve/221214_pc_reserve_store_bar.png"
            alt="reservebar"
          />
          <h3>STARBUCKS RESERVE™ BAR</h3>
          <p>
            스타벅스 리저브 매장에 마련된 리저브 전용 공간에서는
            <br />
            시즌마다 새롭게 선보이는 다채로운 풍미의 스페셜티 커피를
            <br />
            나의 취향에 꼭 맞춘 추출 방식을 선택하여 즐길 수 있습니다.
            <br />
            숙련된 커피 마스터의 친절한 가이드와 함께 만나는 최상의 커피,
            <br />
            스타벅스 리저브 매장에서 경험해 보세요.
            <br />* Starbucks Reserve™ Bar는 전국 리저브R 매장에서 만나볼 수
            있습니다.
          </p>
        </div>
      </div>
      <div className="reserve_part3">
        <div className="reserve_part2_text">
          <h3>BREW GUIDES</h3>
          <p>
            어떤 추출 방식을 선택하는지에 따라 커피가 가진 향과
            풍미는달라집니다.
            <br />
            부드럽고 깔끔한 풍미부터 진하고 강렬한 매력까지,
            <br />
            스페셜티 커피가 다양한 추출 방식을 만나 여러분의 커피 경험을 더욱
            완벽하게 만듭니다.
          </p>
        </div>
        <div className="reserve_part3_img">
          <img src="../../image/reserve/brewguide.png" alt="brewguide" />
        </div>
        <div className="reserve_part3_more">
          <p>매장 별 선택 가능한 추출 방식은 상이할 수 있습니다.</p>
          <Link className="btn">스타벅스 리저브 매장 알아보기</Link>
        </div>
      </div>
    </div>
  );
}
