// 임시이미지
import img from "../image/coffe/coffe1.png";
// import img3 from "../image/coffe/coffe3.png";
import img3 from "../image/coffe/coffebean.png";

import "./main.scss";

// react hook
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import "swiper/css";
import "swiper/css/autoplay";
import "swiper/css/pagination";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination } from "swiper/modules";

const bannerList = [
  { id: 1, title: "배너1", path: "../image/common/slide0.png" },
  { id: 2, title: "배너2", path: "../image/common/slide1.png" },
  { id: 3, title: "배너3", path: "../image/common/slide2.png" },
];

export default function Main() {
  // 배너 데이터 상태 관리
  const [bannerEl, setBannerEl] = useState(bannerList);

  // 프로모션 클래스 유무로 상태 관리
  const [subPromo, setSubpromo] = useState(true);

  const [imgLeftState, setImgLeftState] = useState(-200);
  const [imgOpacityState, setimgOpacityState] = useState(0);

  const imgStyle = {
    left: imgLeftState,
    opacity: imgOpacityState,
  };
  const textStyle = {
    right: imgLeftState,
    opacity: imgOpacityState,
  };

  const [imgLeftState2, setImgLeftState2] = useState(-200);
  const [imgOpacityState2, setimgOpacityState2] = useState(0);
  const imgStyle2 = {
    left: imgLeftState2,
    opacity: imgOpacityState2,
  };
  const textStyle2 = {
    right: imgLeftState2,
    opacity: imgOpacityState2,
  };
  useEffect(() => {
    window.addEventListener("scroll", function () {
      console.log(this.scrollY);
      if (this.scrollY > 422 && this.scrollY <= 1399) {
        setImgLeftState(50);
        setimgOpacityState(1);
      } else {
        setImgLeftState(-200);
        setimgOpacityState(0);
      }

      if ((this.scrollY > 1000) & (this.scrollY < 2100)) {
        setImgLeftState2(-20);
        setimgOpacityState2(1);
      } else {
        setImgLeftState2(-200);
        setimgOpacityState2(0);
      }
    });
  }, []);

  return (
    <div id="main">
      <div className="banner">
        <Swiper
          loop={true}
          pagination={{
            clickable: true,
          }}
          autoplay={{ delay: 2500 }}
          modules={[Pagination, Autoplay]}
          className="mySwiper swiper_ul"
        >
          {bannerEl.map((bv, bi) => {
            return (
              <SwiperSlide key={bi} className="swiper_li">
                <Link to="#">
                  <img src={bv.path} alt="img" />
                </Link>
              </SwiperSlide>
            );
          })}
        </Swiper>
      </div>
      <div className="main_notice_wrap">
        <div className="main_notice">
          <div className="main_notice_title">
            <Link to="/notice">공지사항</Link>
          </div>
          <div className="main_notice_contents">공지사항 롤링</div>
        </div>
        <div className="main_promo">
          <div className="main_promo_title">
            <Link
              onClick={() => {
                if (subPromo === true) {
                  setSubpromo(false);
                } else if (subPromo === false) {
                  setSubpromo(true);
                }
              }}
            >
              프로모션
            <div className="toggle_btn">
              <img src="../../image/common/list_toggle.png" alt="list_btn" 
              className={subPromo === true ? 'up' : 'down'}/>
            </div>
            </Link>
          </div>
        </div>
      </div>
      {/* promotion sub */}
      <div className={subPromo === true ? "subPromo" : "subPromo active"}>
        <div className="promo_wrap">
          <div className="promo_img">
            <img src={img} alt="커피" />
          </div>
          <p>행사 제품을 구매하면 기프트카드가 찾아와요!</p>
          <div className="btn">더보기</div>
        </div>
      </div>
      {/* membership reward */}
      <div className="reward">
        <div className="reward_wrap">
          <div className="reward_logo">
            <img src="../image/logo.png" alt="logo" />
          </div>
          <div className="reward_contents">
            <div className="reward_title">
              <h2>스타벅스 만의 혜택, 스타벅스 리워드</h2>
            </div>
            <div className="reward_content">
              <div>
                <p>
                  <strong>스타벅스 회원이세요?</strong> 로그인을 통해 나만의
                  리워드를 확인해보세요
                </p>
                <p>
                  <strong>스타벅스 회원이 아니세요?</strong> 가입을 통해 리워드
                  혜택을 즐기세요
                </p>
              </div>
              <div className="login_signin">
                <Link to="/login/" className="btn login_btn">
                  로그인
                </Link>
                <Link to="/signup/" className="btn signup_btn">
                  회원가입
                </Link>
              </div>
            </div>
            <div className="reward_content">
              <div>
                <p>
                  회원 가입 후, 스타벅스 e-Gift Card를<br></br>
                  <strong>
                    "나에게 선물하기"로 구매하시고, 편리하게 등록하세요!
                  </strong>
                  <br></br>
                  카드를 등록하여 스타벅스 리워드 회원이 되신 후,<br></br>첫
                  구매를 하시면 무료 음료쿠폰을 드립니다!
                </p>
              </div>
              <div className="gifcard">
                <Link to="" className="btn">
                  기프트카드 선물하기
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="newProduct">
        <div className="newProduct_wrap">
          <div className="newProduct_img">
            <img src={img3} alt="newProduct" style={imgStyle} />
          </div>
          <div className="newProduct_contents" style={textStyle}>
            <div className="newProduct_content">
              <p>
                크리스마스를 더욱 특별하게 만들어 줄 원두
                <br />
                <strong>크리스마스 블렌드 250g</strong>
                <br />
                신선한 라틴 아메리카 원두에 풍부한 인도네시아 원두와
                <br />
                진귀한 숙성 수마트라 원두가 블렌딩 된<br />
                크리스마스 시즌 한정 원두를 소개합니다.
              </p>
            </div>
            <Link className="btn">더보기</Link>
          </div>
        </div>
      </div>

      <div className="pick_favorite">
        <div className="favorite_wrap">
          <div className="img_wrap" style={textStyle2}>
            <img src="../image/coffe/coffe4.png" alt="coffe" />
            <img src="../image/coffe/coffe5.png" alt="coffe" />
            <img src="../image/coffe/coffe6.png" alt="coffe" />
          </div>
          <div className="favorite_contents" style={imgStyle2}>
            <p>PICK</p>
            <p>YOUR</p>
            <p>favorite</p>
            <p>
              <strong>다양한 메뉴</strong>를
              <br /> <strong>스타벅스</strong>에서 즐겨보세요
            </p>
            <p>
              스타벅스만의 특별함을 경험할 수 있는
              <strong>최상의 선택음료</strong>
              <br />
              스타벅스 커피와 완벽한 어울림을 <strong>자랑하는 푸드</strong>
              <br />
              다양한 시도와 디자인으로 <strong>가치를 더하는 상품</strong>
              <br />
              소중한 사람에게 마음을 전하는 가장 좋은 방법
              <strong>스타벅스 카드</strong>
            </p>
          </div>
        </div>
      </div>

      <div className="find_store">
        <div className="store_wrap">
          <div className="store_img">
            <img src="../image/coffe/coffe6.png" alt="store" />
          </div>
          <div className="store_text">
            <h2>
              스타벅스를 가까이에서 경험해보세요.
              <br /> <strong>고객님과 가장 가까이 있는 매장</strong>을
              찾아보세요!
            </h2>
            <p>
              차별화된 커피 경험을 누릴 수 있는
              <strong>
                <Link to="/findstore/">&nbsp;리저브 매장</Link>
              </strong>
              <br />
              다양한 방법으로 편리하게 즐길 수 있는
              <strong>
                <Link to="/findstore/">&nbsp;드라이브 스루 매장</Link>
              </strong>
              <br />
              함께해서 더 따뜻할수 있는 지역사회 소통 공간
              <strong>
                <Link to="/findstore/">&nbsp;커뮤니티 매장</Link>
              </strong>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
