import React from "react";
import ReactPlayer from "react-player";

export default function Coffepress() {
  return (
    <div className="coffepress best_common">
      <div className="coffepress_wrap best_common_wrap">
        <div className="coffepress_title best_common_title">
          <img
            src="../../image/bestcoffe/coffepress/coffeepress.png"
            alt="title"
          />
          <h2>커피 프레스(Coffe Press)</h2>
        </div>
        <div className="video">
          <ReactPlayer
            width={"1100px"}
            height={"500px"}
            url="https://youtu.be/qNIngFRL-Z0"
          />
        </div>
      </div>

      <div className="coffepress_contents best_common_contents">
        <div className="coffepress_content best_common_content">
          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffepress/coffeepress01.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>그라인딩 및 계량하기</h3>
              <p>
                바다 소금 크기로 굵게 그라인딩 된 커피를 프레스에 넣고, 물
                6온스당 커피 2테이블 스푼을 계랑해서 넣어주세요.
              </p>
            </div>
          </div>

          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffepress/coffeepress02.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>뜨거운 물 더하기</h3>
              <p>
                갓 끓인 뜨거운 물을 프레스에 채워주세요. 그라인딩 된 커피를 모두
                적셔주세요.
              </p>
            </div>
          </div>

          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffepress/coffeepress03.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>플런저(plunger) 올리고 커피 추출하기</h3>
              <p>
                플런저를 프레스 위에 올려놓고 4분간 커피를 추출하세요. 이때
                플런저를 아래로 누르지 마세요.
              </p>
            </div>
          </div>

          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffepress/coffeepress04.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>플런저 누르기</h3>
              <p>
                플런저가 프레스 바닥에 닿을 때까지 천천히 아래로 눌러주세요.
                그리고 커피를 즐기면 됩니다.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
