import "./Moccha.scss";
import React from "react";
import Section_Tag from "../../components/Section_Tag";

export default function Moccha() {
  return (
    <div className="moccha">
      <div className="moccha_title">
        <Section_Tag />
        <h2>모카</h2>
      </div>

      <div className="part1_wrap">
        <div className="part1_contents">
          <img src="../../image/moccha/mocha_title.jpg" alt="title" />
          <div className="part1_text">
            <h3>
              “모카는 초콜릿의 달콤함과 커피의 쌉싸름한 맛을 동시에 느낄 수
              있습니다.”
            </h3>
            <p>
              갓 추출한 에스프레소에 모카 시럽을 넣어 잘 섞어줍니다. 여기에 스팀
              밀크를 추가하여 진한 커피 맛과 달콤한 초콜릿 맛이 잘 조화되도록
              합니다.
              <br />
              더욱 달콤한 맛을 원한다면 휘핑크림을 올려 즐겨보세요.
            </p>
          </div>
        </div>
      </div>

      <div className="part2_wrap">
        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/moccha/ice_moccha_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>아이스 카페모카</h4>
            <p>
              진한 초콜릿 모카 시럽과 풍부한 에스프레소를 신선한 우유 그리고
              <br /> 얼음과 섞어 휘핑크림으로 마무리한 음료로 진한 에스프레소와
              초콜릿 맛이 어우러진 커피
            </p>
          </div>
        </div>

        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/moccha/ice_whitemoccha_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>아이스 화이트초코 모카</h4>
            <p>
              달콤하고 부드러운 화이트 초콜릿 시럽과 에스프레소를 신선한 우유
              그리고
              <br /> 얼음과 섞어 휘핑크림으로 마무리한 음료로 달콤함과 강렬한
              에스프레소가 부드럽게 어우러진 커피
            </p>
          </div>
        </div>

        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/moccha/cafemocha_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>카페모카</h4>
            <p>
              진한 초콜릿 모카 시럽과 풍부한 에스프레소를 스팀 밀크와 섞어
              <br /> 휘핑크림으로 마무리한 음료로 진한 에스프레소와 초콜릿 맛이
              어우러진 커피
            </p>
          </div>
        </div>

        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/moccha/mintmoccha_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>클래식 민트 모카</h4>
            <p>
              스타벅스의 클래식인 페퍼민트 모카를 떠올리게 하는 리저브만의
              에스프레소 음료.
              <br /> 오트음료(식물성 대체유)의 고소함과 다크 초콜릿의 쌉쌀함이
              특징인 음료.
            </p>
          </div>
        </div>

        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/moccha/whitemoccha_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>화이트초코 모카</h4>
            <p>
              달콤하고 부드러운 화이트 초콜릿 시럽과 에스프레소를 스팀 밀크와
              섞어
              <br /> 휘핑크림으로 마무리한 음료로 달콤함과 강렬한 에스프레소가
              부드럽게 어우러진 커피
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
