import "./Macchiatto.scss";
import React from "react";
import Section_Tag from "../../components/Section_Tag";

export default function Macchiatto() {
  return (
    <div className="macciatto">
        <div className="macciatto_title">
          <Section_Tag />
          <h2>마끼아또</h2>
        </div>

        <div className="part1_wrap">
          <div className="part1_contents">
            <img
              src="../../image/macchiatto/macchiatto_title.jpg"
              alt="title"
            />
            <div className="part1_text">
              <h3>
                “여러 층으로 이루어진 달콤한 마키아또는 본연의 달콤한 맛이
                느껴질 때까지 부드러운 스팀 밀크가 먼저 입안을 채웁니다.”
              </h3>
              <p>
                스팀 밀크 다음으로 크리미한 우유 거품이 올라가며, 진한
                에스프레소를 부어 윗부분에 갈색 마크가 형성됩니다.
                <br />
                마지막으로 바리스타가 캐러멜 드리즐을 그물 모양으로 뿌리면 마치
                예술 작품과 같은 마키아토가 완성됩니다.
              </p>
            </div>
          </div>
        </div>

        <div className="part2_wrap">
          <div className="part2_contents">
            <div className="part2_img">
              <img
                src="../../image/macchiatto/ice_macchiatto_thumb.jpg"
                alt="thumb"
              />
            </div>
            <div className="part2_text">
              <h4>아이스 마끼아또</h4>
              <p>
                향긋한 바닐라 시럽과 시원한 우유와 얼음을 넣고 점을 찍듯이
                <br /> 에스프레소를 부은 후 벌집 모양으로 카라멜 드리즐을 올린
                달콤한 커피 음료
              </p>
            </div>
          </div>

          <div className="part2_contents">
            <div className="part2_img">
              <img
                src="../../image/macchiatto/caramel_macchiatto_thumb.jpg"
                alt="thumb"
              />
            </div>
            <div className="part2_text">
              <h4>카라멜 마끼아또</h4>
              <p>
                향긋한 바닐라 시럽과 따뜻한 스팀 밀크 위에 풍성한 우유 거품을
                얹고 점을 찍듯이
                <br /> 에스프레소를 부은 후 벌집 모양으로 카라멜 드리즐을 올린
                달콤한 커피 음료
              </p>
            </div>
          </div>
        </div>
    </div>
  );
}
