import "./store.scss";
import React from "react";
import Section_Tag from "../../components/Section_Tag";
import { Link } from "react-router-dom";

export default function Community() {
  return (
    <div className="community">
      <div className="community_title">
        <Section_Tag />
        <h2>커뮤니티 스토어</h2>
      </div>
      <div className="community_part1">
        <img src="../../image/community/community_store_vis.jpg" alt="commu" />
        <div className="community_part1_text">
          <h3>"소통의 공간이 된 스타벅스 커뮤니티 스토어 매장"</h3>
          <p>
            스타벅스 커뮤니티 스토어는 지역사회가 해결해야 하는 문제를 찾아
            현명한 해결책을 모색하는 소셜 허브 역할을 하고 있습니다.
            <br />
            뿐만 아니라, 지역사회의 긍정적인 변화와 장기적인 발전을 위해 매출의
            일부를 비영리 단체에 전달하여 사회에 환원하는 이익 공유형 매장을
            운영하는 차별화된 글로벌 사회 공헌 모델이기도 합니다.
            <br />
            국내에서는 젊음과 소통의 상징성을 가진 대학로 지역 커뮤니티 스토어
            대학로점을 운영 중에 있으며, 문화 특강이나 커피 세미나 등 다양한
            프로그램을 통해 지역사회의 열린 공간으로 자리 잡았습니다.
            <br />
            ‘Youth Leadership’ 콘셉트로 대학생 고객과 협업으로 탄생한 대형
            아트월과 전 세계 최초 디지털 커뮤니케이션 보드, 커뮤니티 스토어의
            메시지를 담은 인포메이션 카드 등을 만나볼 수 있습니다.
          </p>
        </div>
      </div>
      <div className="community_part2">
        <div className="commu_part2_item">
          <img src="../../image/community/cs_pic1.png" alt="commucircle" />
        </div>
        <div className="commu_part2_item">
          <div className="commu_part2_item_inner">
            <h3>
              "스타벅스 커뮤니티 스토어는 고객이 매장에서 구매하는 모든 품목 당
              300원을 적립하여 사회에 환원합니다."
            </h3>
            <p>
              스타벅스는 지역사회의 긍정적인 변화와 장기적인 발전을 위해 커피 및
              음료, 푸드, 상품 등 고객이 매장에서 구매하는 모든 품목 당 300원을
              적립해 지역사회에 환원하는 사회 공헌 활동을 하고 있습니다.
            </p>
          </div>
        </div>
      </div>
      <div className="community_part3">
        <div className="commu_part3_item">
          <div className="commu_part3_item_inner">
            <h3>
              "커뮤니티 스토어는 청년 인재를 양성하는데 앞장서고 있습니다."
            </h3>
            <p>
              커뮤니티 스토어에서 적립된 기금은 장학금으로 지급되며, 이외에도
              인턴십 프로그램, 봉사활동 지원 및 바리스타 교육 등을 통해 청년
              인재를 양성하는데 앞장서고 있습니다.
            </p>
          </div>
        </div>
        <div className="commu_part3_item">
          <img src="../../image/community/cs_pic2.png" alt="commucircle2" />
        </div>
      </div>
      
      <div className="find_through">
        <Link to="/findstore"><p>전국의 커뮤니티 스토어를 확인해 보세요!</p></Link>
      </div>
    </div>
  );
}
