import "./mainFix.scss";
import logo from "../image/logo.png";

import { mainList, subList } from "./data";
import { Link } from "react-router-dom";
import { useState } from "react";

export default function Header() {
  const [menu, setMenu] = useState(mainList);
  const [subMenu, setSubMenu] = useState(subList);
  const [on, setOn] = useState(false);
  const [style, setStyle] = useState(0);

  // function ehandler(i) {
  //   setOn(i);
  // }

  /*   const subStyle = {
    maxHeight: style,
  }; */

  return (
    <div id="header">
      <div className="header_wrap">
        <div className="logo">
          <h1 className="hidden">스타벅스</h1>
          <Link to="/">
            <img src="../image/logo.png" alt="logo" />
          </Link>
        </div>
        <div className="main_menu_wrap">
          <ul className="main_gnb">
            <li><Link to='/signup/'>회원가입</Link></li>
            <li><Link to='/login/'>로그인</Link></li>
            <li>
              <form>
                <input type="text" id="input_search" />
                <label htmlFor="input_search">
                  <img
                    src="../../image/common/search_icon.png"
                    alt="search_icon"
                  />
                </label>
              </form>
            </li>
          </ul>
          <ul className="main_menu">
            {/* 메인메뉴 */}
            {menu.map((mv, mi) => {
              return (
                <li key={mi}>
                  <Link to="#">{mv.title}</Link>
                  <ul
                    className="sub_menu" /*  style={mi === on ? subStyle : null} */
                  >
                    {/* 메인메뉴 id === 서브메뉴 id */}
                    {subMenu.map((sv, si) => {
                      if (mv.id === sv.id) {
                        return (
                          <li
                            key={si} /* className={mi === on ? "on" : null} */
                          >
                            {sv.title.map((stv, sti) => {
                              return (
                                <Link key={sti} to={sv.path[sti]}>
                                  {stv}
                                </Link>
                              );
                            })}
                          </li>
                        );
                      }
                    })}
                  </ul>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    </div>
  );
}
