import React from "react";
import ReactPlayer from "react-player";

export default function CoffeMaker() {
  return (
    <div className="coffemaker best_common">
      <div className="coffemaker_wrap best_common_wrap">
        <div className="coffemaker_title best_common_title">
          <img
            src="../../image/bestcoffe/coffemaker/coffemaker.png"
            alt="title"
          />
          <h2>커피 메이커(Coffe Maker)</h2>
        </div>
        <div className="video">
          <ReactPlayer
            width={"1100px"}
            height={"500px"}
            url="https://youtu.be/mT2ZJnLyjXg"
          />
        </div>
      </div>

      <div className="coffemaker_contents best_common_contents">
        <div className="coffemaker_content best_common_content">
          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffemaker/coffemaker01.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>취향에 맞는 커피 찾기</h3>
              <p>
                바닥이 평평한 필터의 경우, 바다 소금 정도의 중간 크기로 그라인딩
                된 커피를 사용하세요. 콘 형태 필터의 경우, 설탕 알갱이 크기의
                곱게 그라인딩 된 커피를 사용하세요.
              </p>
            </div>
          </div>

          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffemaker/coffemaker02.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>계량하기</h3>
              <p>
                물 6온스 당 신선하게 그라인딩 된 커피 2테이블 스푼을 계랑하세요.
              </p>
            </div>
          </div>

          <div className="item">
            <div className="item_img">
              <img
                src="../../image/bestcoffe/coffemaker/coffemaker03.jpg"
                alt="img"
              />
            </div>
            <div className="item_text">
              <h3>커피 만들어 즐기기</h3>
              <p>
                브루어를 이용해 만든 커피는 신선하게 마셔야 가장 맛있습니다. 한
                번에 마실 정도의 커피를 만드세요. 커피를 다시 데우면 풍미가
                약해질 수 있습니다.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
