import React from "react";
import { decaffeineList } from "../data";

export default function Farm_decaffeine() {
  return (
    <div className="farm_decaffeine">
      <div className="part1_wrap">
        <div className="part1">
          <div className="part1_title">
            <h3>스타벅스 디카페인</h3>
          </div>
          <div className="part1_contents">
            <img src={decaffeineList[0].path} alt="title" />
            <div className="part1_text">
              <h3>“Our newest coffee to love”</h3>
              <p>
                “언제나 풍부한 커피를 즐길 수 있는 디카페인” 부드럽고 편안한
                아침을 마주하고 싶은 순간, 바쁘게 보낸 하루의 마무리 즈음, 혹은
                커피를 많이 마시게 되는 날이라면 스타벅스 디카페인 커피를
                추천드립니다. 디카페인으로 즐길 수 있는 다양한 스타벅스 음료를
                만나 보세요.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="part2_wrap">
        <div className="part2">
          <div className="part2_title hidden">
            <h3>스타벅스 디카페인</h3>
          </div>
          <div className="part2_contents">
            <img src={decaffeineList[1].path} alt="title" />
            <div className="part2_text">
              <h3>“커피의 맛과 풍미는 변함없는 스타벅스 디카페인 커피”</h3>
              <p>
                스타벅스의 디카페인 커피는 식품위생법 등에서 허용하는 디카페인
                기준에 적합한 CO2 추출방식으로 만들어집니다. 화학제품은 사용하지
                않고 오직 CO2 와 스팀만을 사용하여 원두 속의 카페인을 제거하는
                방식으로 만들며, 카페인 부담은 줄이면서 스타벅스 커피만의 뛰어난
                맛과 풍미는 그대로 즐기실 수 있습니다.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="part3_wrap">
        <div className="part3">
          <div className="part3_title">
            <h3>일반 커피와 디카페인 커피의 차이는 무엇인가요?</h3>
            <p>
              스타벅스 디카페인 커피는 화학제품을 쓰지 않고 오직 카페인만을
              제거하는 CO2 방식으로 만들어집니다. 일반 커피 대비 맛과 향의 차이가
              거의 없는 것이 가장 큰 특징입니다.
            </p>
          </div>
  
          <div className="part3_contents">
            <div className="part3_content_title">
              <h3>스타벅스 디카페인 커피를 만드는 CO2 추출 방식을 소개합니다.</h3>
            </div>
  
            <div className="part3_items">
              <div className="item">
                <img src={decaffeineList[2].path} alt="img" />
                <p>
                  <strong>1</strong>생두를 깨끗하게 세척한 뒤, 증기와 물을 이용해 생두를 불립니다.
                </p>
              </div>
              <div className="item">
                <img src={decaffeineList[3].path} alt="img" />
                <p>
                  <strong>2</strong>카페인을 추출할 수 있는 스테인리스 스틸 용기에 물에 불린 생두를
                  넣습니다.
                </p>
              </div>
              <div className="item">
                <img src={decaffeineList[4].path} alt="img" />
                <p>
                  <strong>3</strong>액체 상태의 CO2가 높은 압력에서 생두와 접촉하여 생두의 카페인만
                  추출하는 역할을 하고 커피의 맛과 향은 그대로 보존됩니다.
                </p>
              </div>
              <div className="item">
                <img src={decaffeineList[5].path} alt="img" />
                <p>
                  <strong>4</strong>디카페인 공정을 마친 생두는 스타벅스만의 탁월한 로스팅과 커핑을
                  거쳐 스타벅스 디카페인 에스프레소 로스트 원두로 탄생합니다.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
