import "./App.scss";
import { Route, Routes } from "react-router-dom";

// main
import Footer from "./components/Footer";
import Header from "./components/Header";
import Main from "./router/Main";
import ItemList from "./router/ItemList";

// store
import FindStore from "./router/store/FindStore";
import FindLocal from "./router/store/FindLocal";
import DriveThrough from "./router/store/DriveThough";
import Reseve from "./router/store/Reseve";
import Community from "./router/store/Community";

//coffebean
import Starbucksbean from "./router/coffebean/Starbucksbean";
import Storyfarm from "./router/coffebean/Storyfarm";
import Espresso from "./router/coffebean/Espresso";
import Americano from "./router/coffebean/Americano";
import Macchiatto from "./router/coffebean/Macchiatto";
import Cappuccino from "./router/coffebean/Cappuccino";
import Moccha from "./router/coffebean/Moccha";
import BestCoffe from "./router/coffebean/BestCoffe";

// login
import Login from "./router/login/Login";

// detail
import Detail from "./router/Detail";
import Signup from "./router/login/Signup";

function App() {
  return (
    <div className="App">
      <Header></Header>
      <Routes>
        <Route
          path="/"
          element={
            <>
              <Main></Main>
              <ItemList></ItemList>
            </>
          }
        />

        {/* store */}
        <Route path="/findstore/" element={<FindStore />} />
        <Route path="/findlocal/" element={<FindLocal />} />
        <Route path="/drivethrough/" element={<DriveThrough />} />
        <Route path="/reserve/" element={<Reseve />}></Route>
        <Route path="/storecomunity/" element={<Community />}></Route>

        {/* coffebean */}
        <Route path="/buckscoffebean/" element={<Starbucksbean />}></Route>
        <Route path="/storyfarm/" element={<Storyfarm/>}></Route>
        <Route path="/espresso/" element={<Espresso/>}/>
        <Route path="/americano/" element={<Americano/>}></Route>
        <Route path="/macchiatto" element={<Macchiatto/>}></Route>
        <Route path="/cappuccino/" element={<Cappuccino/>} ></Route>
        <Route path="/moccha/" element={<Moccha/>}></Route>
        <Route path="/bestcoffe/" element={<BestCoffe/>}></Route>

        {/* detail */}
        <Route path="/detail/:id" element={<Detail />} />

        {/* login */}
        <Route path="/login/" element={<Login/>}></Route>
        <Route path="/signup/" element={<Signup/>}></Route>
      </Routes>

      <Footer />
    </div>
  );
}

export default App;
