import './main.scss';

import React, { useState } from "react";
import { itemList } from "../components/data";
import { useNavigate } from "react-router-dom";

const listStyle = {

};

export default function ItemList(props) {
  const navigate = useNavigate();
  const [itemEl, setItemEl] = useState(itemList);


  function cp(i) {
    navigate(`/detail/${i+1}`);
  }

  return (
    <div className="itemList"/*  style={listStyle} */>
      <h2>신제품 출시!</h2>
      {itemEl.map((v, i) => {
        return (
          <div key={i} onClick={()=>{ 
            cp(i)
          }} className="item">
            <img src={v.path} alt="img"/>
            <div>{v.title}</div>
            {/* <div>가격 : {v.price}</div> */}
          </div>
        );
      })}
    </div>
  );
}
