import "./Storyfarm.scss";
import "../../components/storyfarm_sub/Farm_sub.scss"

import React, { useState } from "react";
import Section_Tag from "../../components/Section_Tag";
import { Link } from "react-router-dom";

// 서브컴포넌트
import Farm_story from "../../components/storyfarm_sub/Farm_story";
import Farm_arabica from "../../components/storyfarm_sub/Farm_arabica";
import Farm_spectrum from "../../components/storyfarm_sub/Farm_spectrum";
import Farm_decaffeine from "../../components/storyfarm_sub/Farm_decaffeine";

export default function Storyfarm() {
  const [comNum, setComNum] = useState(0);
  function numHandler(i) {
    setComNum(i);
  }

  const storyMenuList = [
    { id: 1, title: "농장에서 우리손으로" },
    { id: 2, title: "최상의 아라비카 원두" },
    { id: 3, title: "스타벅스 로스트 스펙트럼" },
    { id: 4, title: "스타벅스 디카페인" },
  ];

  return (
     <div className="story_farm_wrap">
        <div className="story_farm_title">
          <Section_Tag />
          <h2>커피 이야기</h2>
        </div>
        <ul className="story_farm_menu">
          {storyMenuList.map((v, i) => {
            return (
              <li key={i} className={i === comNum ? "on" : null}>
                <Link
                  to="#"
                  onClick={() => {
                    numHandler(i);
                  }}
                >
                  {v.title}
                </Link>
              </li>
            );
          })}
        </ul>
        <div className="section">
          {comNum === 0 ? (
            <Farm_story />
          ) : null || comNum === 1 ? (
            <Farm_arabica />
          ) : null || comNum === 2 ? (
            <Farm_spectrum />
          ) : null || comNum === 3 ? (
            <Farm_decaffeine />
          ) : null}
        </div>
     </div>
  );
}
