import "./BestCoffe.scss";
import "../../components/bestcoffe_sub/Best_sub.scss"

import React, { useState } from "react";
import { Link } from "react-router-dom";

import Section_Tag from "../../components/Section_Tag";

// 서브컴포넌트
import Coffepress from "../../components/bestcoffe_sub/Coffepress";
import Pourover from "../../components/bestcoffe_sub/PourOver";
import IcePourOver from "../../components/bestcoffe_sub/IcePourOver";
import CoffeMaker from "../../components/bestcoffe_sub/CoffeMaker";

export default function BestCoffe() {
  const [comNum, setComNum] = useState(0);
  function numHandler(i) {
    setComNum(i);
  }

  const storyMenuList = [
    { id: 1, title: "커피 프레스" },
    { id: 2, title: "푸어 오버" },
    { id: 3, title: "아이스 푸어 오버" },
    { id: 4, title: "커피 메이커" },
  ];

  return (
    <div className="bestCoffe">
      <div className="bestCoffe_title">
        <Section_Tag />
        <h2>커피 이야기</h2>
      </div>
      <ul className="bestCoffe_menu">
        {storyMenuList.map((v, i) => {
          return (
            <li key={i} className={i === comNum ? "on" : null}>
              <Link
                to="#"
                onClick={() => {
                  numHandler(i);
                }}
              >
                {v.title}
              </Link>
            </li>
          );
        })}
      </ul>
      <div className="section">
        {comNum === 0 ? (
          <Coffepress />
        ) : null || comNum === 1 ? (
          <Pourover />
        ) : null || comNum === 2 ? (
          <IcePourOver />
        ) : null || comNum === 3 ? (
          <CoffeMaker />
        ) : null}
      </div>
    </div>
  );
}
