import { useState } from "react";
import { signupData } from "./signupData";

export default function Signup() {
  const [on, setOn] = useState([false], [false], [false], [false]);
  const [check, setCheck] = useState([false], [false], [false], [false]);

  function setCheckArray(e) {
    let newCheck = [...check];
    newCheck[e - 1] = !newCheck[e - 1];
    setCheck(newCheck);
  }

  function setOnArray(e) {
    let newOn = [...on];
    newOn[e - 1] = !newOn[e - 1];
    setOn(newOn);
  }

  // const [state, setState] = useState("");

  function handler(e) {
    if (e === 1) {
      setOnArray(e);
      setCheckArray(e);
    }
    if (e === 2) {
      setOnArray(e);
      setCheckArray(e);
    }
    if (e === 3) {
      setOnArray(e);
      setCheckArray(e);
    }
    if (e === 4) {
      setOnArray(e);
      setCheckArray(e);
    }
  }

  return (
    <div className="signup">
      <div className="signup_title">
        <h2>회원가입</h2>
      </div>
      <div className="signup_wrap">
        <div className="signup_wrap_title">
          <img src="../../image/login/signup_title.png" alt="signup_title" />
          <p>회원가입 약관동의 및 본인인증단계입니다.</p>
        </div>
        <form className="signup_clause">
          {signupData.map((v, i) => {
            // 필수사항
            if (i <= 2) {
              return (
                <div className="item" key={v.id}>
                  <div className="item_title">
                    <span>
                      {v.title}
                      <strong>(필수)</strong>
                    </span>
                    <label
                      htmlFor={`clause_check${i + 1}`}
                      className={
                        on[i] === true && check[i] === true ? "on" : null
                      }
                      onClick={() => {
                        handler(i + 1);
                      }}
                    >
                      <div className="check_path"></div>
                    </label>
                    <input
                      type="checkbox"
                      id={`clause_check${i + 1}`}
                      checked={
                        on[i] === true && check[i] === true ? true : false
                      }
                      required
                    />
                  </div>
                  <textarea
                    name={`clause${i + 1}`}
                    id={`clause${i + 1}`}
                    disabled
                  >
                    {v.content}
                  </textarea>
                </div>
              );
            }
            // 선택사항
            if (i >= 3) {
              return (
                <div className="item" key={v.id}>
                  <div className="item_title">
                    <span>
                      {v.title}
                      <strong>(선택)</strong>
                    </span>
                    <label
                      htmlFor={`clause_check${i + 1}`}
                      className={
                        on[i] === true && check[i] === true ? "on" : null
                      }
                      onClick={() => {
                        handler(i + 1);
                      }}
                    >
                      <div className="check_path"></div>
                    </label>
                    <input
                      type="checkbox"
                      id={`clause_check${i + 1}`}
                      checked={
                        on[i] === true && check[i] === true ? true : false
                      }
                    />
                  </div>
                  <div
                    className="textarea"
                    name={`clause${i + 1}`}
                    id={`clause${i + 1}`}
                    disabled
                  >
                    <table>
                      <tbody>
                        <tr>
                          <th scope="row">목적</th>
                          <td>
                            마케팅 정보 활용(상품정보 및 행사 정보 안내 등)
                          </td>
                        </tr>
                        <tr>
                          <th scope="row">항목</th>
                          <td>휴대전화 번호, 메일</td>
                        </tr>
                        <tr>
                          <th scope="row">보유 및 이용 기간</th>
                          <td>회원 탈퇴 시까지</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  <p>
                    ※ 귀하께서는 동의를 거부하실 수 있으나, 동의를 거부 하실
                    경우 마케팅 정보를 받으실 수 없습니다.
                  </p>
                </div>
              );
            }
          })}

          {/* 주의사항 및 인증방식 */}
          <div className="caution">
            <div className="caution_title">
              <h2>회원가입 유의사항</h2>
            </div>
            <ul className="coution_content">
              <li>반드시 회원님 명의로 된 휴대폰, 아이핀을 이용해주세요.</li>
              <li>
                타인의 개인정보를 도용하여 가입할 경우 향후 적발 시 서비스
                이용제한 및 법적 제재를 받으실 수 있습니다.
              </li>
              <li>
                스타벅스 코리아의 공식 홈페이지는 Internet Explorer 9.0 이상,
                Chrome, Firefox, Safari 브라우저에 최적화 되어있습니다.
              </li>
            </ul>
          </div>

          <div className="certify">
            <div className="certify_title">
              <h2>인증방식 선택</h2>
            </div>
            <div className="certify_wrap">
              <div className="phone common_certify">
                <img src="../../image/login/signup_phone.png" alt="phone" />
                <h3>휴대폰인증</h3>
                <p>본인 명이의 휴대폰을 통해 인증합니다</p>
                <input type="submit" value="인증하기" className="btn" />
              </div>

              <div className="ipin common_certify">
                <img src="../../image/login/signup_ipin.png" alt="ipin" />
                <h3>아이핀인증</h3>
                <p>가입한 아이핀ID로 인증합니다</p>
                <input type="submit" value="인증하기" className="btn" />
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
