import "./Cappuccino.scss";
import React from "react";
import Section_Tag from "../../components/Section_Tag";

export default function Cappuccino() {
  return (
    <div className="cappuccino">
      <div className="cappuccino_title">
        <Section_Tag />
        <h2>카푸치노</h2>
      </div>

      <div className="part1_wrap">
        <div className="part1_contents">
          <img src="../../image/cappuccino/cappuccino_title.jpg" alt="title" />
          <div className="part1_text">
            <h3>“카푸치노는 바리스타가 가진 가장 고도화된 기술입니다.”</h3>
            <p>
              스타벅스의 바리스타는 카푸치노에 공기가 들어간 가벼운 거품과
              맛있는 에스프레소가 만나 조화를 이루는 시점에 대해 고도의
              트레이닝을 하며 테스트를 통과해야만 합니다.
              <br />
              무게에 따라 더 가벼운 카푸치노와 혹은 더 무거운 카푸치노를
              원한다면 바리스타에게 미리 요청하세요. 취향에 맞는 카푸치노를
              제조해드립니다.
            </p>
          </div>
        </div>
      </div>

      <div className="part2_wrap">
        <div className="part2_contents">
          <div className="part2_img">
            <img
              src="../../image/cappuccino/ice_cappuccino_thumb.jpg"
              alt="thumb"
            />
          </div>
          <div className="part2_text">
            <h4>아이스 카푸치노</h4>
            <p>
              풍부하고 진한 에스프레소에 신선한 우유와 우유 거품이
              <br /> 얼음과 함께 들어간 시원하고 부드러운 커피 음료
            </p>
          </div>
        </div>

        <div className="part2_contents">
          <div className="part2_img">
            <img
              src="../../image/cappuccino/cappucchino_thumb.jpg"
              alt="thumb"
            />
          </div>
          <div className="part2_text">
            <h4>카푸치노</h4>
            <p>
              풍부하고 진한 에스프레소에 따뜻한 우유와 벨벳 같은 우유 거품이
              <br /> 1:1 비율로 어우러져 마무리된 커피 음료
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
