import "./store.scss";
import { Link } from "react-router-dom";
import Section_Tag from "../../components/Section_Tag";

export default function DriveThrough() {
  return (
    <div className="driveThrough">
      <div className="drive_title">
        <Section_Tag/><h2>드라이브스루 매장</h2>
      </div>
      <div className="driveimg">
        <img
          src="../../image/drivethrough/drive_store_fimg01.png"
          alt="drive_store"
        />
        <p>
          <strong>"가끔은 내리지 않고 즐기셔도 좋습니다."</strong>
        </p>
        <p>
          <br />
          차 안 가득히 퍼져나가는 커피향과 여운, 스타벅스 드라이브 스루 매장에서
          함께 태워가세요!
          <br />
          스타벅스 드라이브 스루(Drive Thru)는 고객이 주차를 하지 않고 차를 탄
          채로
          <br /> 스타벅스의 최고의 서비스를 편리하게 이용할 수 있는 서비스
          입니다.
          <br />
          이동 중에도 부담 없이 이용할 수 있으며, 스타벅스 최초 Face to Face
          화상 주문 시스템으로
          <br /> 주문과정에서 부터 결제까지 친절한 스타벅스 파트너의 미소가
          함께합니다.
        </p>
      </div>

      <div className="drivehow">
        <div className="drive_bg">
          <div className="drive_circle1">
            <img src="../../image/drivethrough/myCircle01.png" alt="circle" />
            <p>step1 : 스타벅스 드라이브스루 매장 방문</p>
          </div>
          <div className="drive_circle2">
            <img src="../../image/drivethrough/myCircle02.png" alt="circle" />
            <p>step2 : 차안에서 화상으로 편하게 주문하기</p>
          </div>
          <div className="drive_circle3">
            <img src="../../image/drivethrough/myCircle03.png" alt="circle" />
            <p>step3 : 스타벅스 음료 즐기기</p>
          </div>
        </div>
        <div className="drive_rail"></div>
        <div className="drive_car">
          <img src="../../image/drivethrough/drive_car.png" alt="drive_car" />
        </div>
      </div>

      <div className="didyouKnow">
        <div className="didyouimg">
          <img
            src="../../image/drivethrough/drive_through_img01.png"
            alt="didyouknow"
          />
        </div>
        <div className="didyoutext">
          <p>스타벅스 최초 화상 주문시스템</p>
          <h3>
            "고객과 눈을 맞추고, 경청하며, 정을 나누는 스타벅스만의 철학과<br/>한국적
            정서를 모두 담아낸 시스템입니다.""
          </h3>
          <p>
            스타벅스 드라이브 스루 매장에서는 국내 업계뿐만 아니라
            <br />
            전 세계 스타벅스 최초로 설치된 화상 주문 시스템을 경험할 수
            있습니다.
            <br />
            드라이브 스루 매장을 방문한 고객은 대형 스마트 패널을 통해
            바리스타와 직접 소통합니다.
            <br />
            이는 고객과 눈을 맞추고, 경철하며, 정을 나누는 스타벅스만의 철학과
            <br /> 한국적 정서를 모두 담아낸 시스템입니다.
          </p>
        </div>
      </div>
      <div className="find_through">
        <Link to="/findstore"><p>전국의 드라이브 스루 매장을 확인해 보세요!</p></Link>
      </div>
    </div>
  );
}
