import "./Espresso.scss";
import Section_Tag from "../../components/Section_Tag";
import React from "react";

export default function Espresso() {
  return (
    <div className="esspresso">
      <div className="esspresso_title">
        <Section_Tag />
        <h2>에스프레소</h2>
      </div>

      <div className="part1_wrap">
        <div className="part1_contents">
          <img src="../../image/espresso/espresso_title.jpg" alt="title" />
          <div className="part1_text">
            <h3>“에스프레소 마키아또는 전통적인 유럽 스타일의 음료입니다.”</h3>
            <p>
              에스프레소 마키아또는 전통적인 유럽 스타일 음료로 완벽하게 추출된
              에스프레소 샷에 부드러운 우유 거품이 더해집니다.
              <br />
              거품을 너무 많이 넣으면 에스프레소의 강렬하고 진한 풍미와 캐러멜
              풍미를 가릴 수 있으므로, 거품은 아주 적은 양만 올립니다.
            </p>
          </div>
        </div>
      </div>

      <div className="part2_wrap">
        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/espresso/espresso_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>에스프레소 마키아또</h4>
            <p>
              신선한 에스프레소 샷에 우유 거품을 살짝 얹은 커피 음료로써, 강렬한
              에스프레소의 맛과 우유의 부드러움을 같이 즐길 수 있는 커피 음료
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
