import React, { useState } from "react";
import { itemList } from "../components/data";
import ItemList from "./ItemList";
import { Routes, useNavigate, useParams } from "react-router-dom";

export default function Detail() {
  const { id } = useParams();
  const navi = useNavigate();
  const [count, setCount] = useState(1);
  function handler(e) {
    if (e.target.value === "remove") {
      setCount(count - 1);
      if (count <= 0) {
        setCount(0);
      }
    } else if (e.target.value === "add") {
      setCount(count + 1);
    }
  }

  return (
    <div id="detail">
      <div className="detail_wrap">
        {itemList.map((v, i) => {
          if (parseInt(id) === v.id) {
            return (
              <div className="item" key={i}>
                <div className="item_img">
                  <img src={v.path} alt="img" />
                </div>
                <div className="item_title">
                  <h2>{v.title}</h2>
                </div>
                <form className="count_num" onSubmit={(e)=>{
                  e.preventDefault();
                }}>
                  <div className="btn_wrap">
                    <button
                      onClick={handler}
                      value={"remove"}
                      name="remove_btn"
                    >
                      -
                    </button>
                    <div className="item_num">{count}</div>
                    <button onClick={handler} value={"add"} name="add_btn">
                      +
                    </button>
                  </div>
                  <input
                    type="submit"
                    name="submit_btn"
                    id="submit_btn"
                    className="btn"
                    value={"장바구니에 담기"}
                    onClick={()=>{
                      navi('/');
                    }}
                  />
                </form>
              </div>
            );
          }
        })}
      </div>
    </div>
  );
}
