import { useState } from "react";
import "./login.scss";
import { Link } from "react-router-dom";

export default function Login() {
  const [on, setOn] = useState("");

  return (
    <div className="login">
      <div
        className="formbg"
        onClick={(e) => {
          setOn("");
        }}
      ></div>
      <form>
        <label
          htmlFor="login_id"
          className="id_label"
          onClick={() => {
            setOn("login_id");
          }}
        >
          <span className={on === "login_id" ? "on" : null}>
            아이디를 입력해주세요
          </span>
          <input type="text" id="login_id" />
        </label>

        <label
          htmlFor="login_pw"
          className="pw_label"
          onClick={() => {
            setOn("login_pw");
          }}
        >
          <span className={on === "login_pw" ? "on" : null}>
            비밀번호를 입력 해 주세요
          </span>
          <input type="password" id="login_pw" />
        </label>

        <input type="submit" id="login_submit" className="btn" value="로그인" />

        <div className="other_login">
          <ul className="login_icons">
            <li>
              <Link to="#">
                <img src="../../image/login/kakao.png" alt="kakao" />
              </Link>
            </li>
            <li>
              <Link to="#">
                <img src="../../image/login/naver.png" alt="naver" />
              </Link>
            </li>
            <li>
              <Link to="#">
                <img src="../../image/login/google.png" alt="google" />
              </Link>
            </li>
            <li>
              <Link to="#">
                <img src="../../image/login/facebook.png" alt="facebook" />
              </Link>
            </li>
          </ul>
        </div>
      </form>
    </div>
  );
}
