import "./Starbucksbean.scss";

import React, { useState } from "react";
import { beanList, viaList, atHomeList } from "../../components/data";
import Section_Tag from "../../components/Section_Tag";

export default function Starbucksbean() {
  const [on, setOn] = useState(true);
  const [slide, setSlide] = useState("bean");

  const [bean, setBean] = useState(beanList);
  const [via, setVia] = useState(viaList);
  const [atHome, setAthome] = useState(atHomeList);

  let coffebeanList = [];
  if (slide === "bean") {
    coffebeanList = bean;
  } else if (slide === "via") {
    coffebeanList = via;
  } else if (slide === "athome") {
    coffebeanList = atHome;
  }

  return (
    <div className="starbucksbean">
      <div className="starbucksbean_title">
        <Section_Tag/><h2>스타벅스 커피빈</h2>
      </div>
      <div className="starbucksbean_index">
        <div className="starbucksbean_index_inner">
          {/* <h2>분류</h2> */}
          <ul className="beanList">
            <li
              onClick={() => {
                setSlide("bean");
                if (slide !== "bean") {
                  setOn(true);
                }
              }}
              className={slide === "bean" ? "on" : null}
            >
              스타벅스 원두
            </li>
            <li
              onClick={() => {
                setSlide("via");
                if (slide !== "via") {
                  setOn(true);
                }
              }}
              className={slide === "via" ? "on" : null}
            >
              스타벅스 비아
            </li>
            <li
              onClick={() => {
                setSlide("athome");
                if (slide !== "athome") {
                  setOn(true);
                }
              }}
              className={slide === "athome" ? "on" : null}
            >
              스타벅스 엣홈 by 캡슐
            </li>
          </ul>
        </div>
      </div>

      <div className="starbucksbean_list">
        {/* 스타벅스 원두 */}
        {slide === "bean" ? (
          <div className="startbucksbean_list_inner">
            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>블론드 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "bean") {
                  if (v.id <= 2) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>미디엄 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "bean") {
                  if (v.id >= 3 && v.id <= 10) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>다크 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "bean") {
                  if (v.id >= 11 && v.id <= 12) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>리저브 원두</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "bean") {
                  if (v.id >= 13) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>
          </div>
        ) : null}

        {/* 스타벅스 비아 */}
        {slide === "via" ? (
          <div className="startbucksbean_list_inner">
            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>미디엄 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "via") {
                  if (v.id <= 5) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>다크 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "via") {
                  if (v.id >= 6 && v.id <= 6) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>플레이버</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "via") {
                  if (v.id >= 7) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>
          </div>
        ) : null}

        {/* 스타벅스 엣홈 */}
        {slide === "athome" ? (
          <div className="startbucksbean_list_inner">
            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>블론드 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "athome") {
                  if (v.id <= 1) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>미디엄 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "athome") {
                  if (v.id >= 2 && v.id <= 4) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>다크 로스트</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "athome") {
                  if (v.id >= 5 && v.id <= 7) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>

            <div className="startbucksbean_list_inner_items">
              <div className="startbucksbean_list_inner_items_title">
                <h2>플레이버 라떼</h2>
              </div>
              {coffebeanList.map((v, i) => {
                if (slide === "athome") {
                  if (v.id >= 8) {
                    return (
                      <div className="bean_item" key={v.id}>
                        <div className="item">
                          <img src={v.path} alt="beanimg" />
                          <div>{v.title}</div>
                        </div>
                      </div>
                    );
                  }
                }
              })}
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
}
