import React from "react";
import { arabicaList } from "../data";

// 비디오플레이어
import ReactPlayer from "react-player";

export default function Farm_arabica() {
  return (
    <div className="farm_arabica">
      <div className="part1_wrap">
        <div className="part1">
          <div className="part1_title">
            <h3>최상의 아라비카 원두</h3>
          </div>
          <div className="part1_contents">
            <img src={arabicaList[0].path} alt="title" />
            <img src={arabicaList[1].path} alt="title_icon" />
            <div className="part1_text">
                <div className="video">
                  <ReactPlayer url="https://youtu.be/G63W2Rv4zY0" />
                </div>
              <div className="text">
                <h3>"최고의 커피는 최상의 원두로부터"</h3>
                <p>
                  스타벅스 아라비카 원두는 고품질, 그 이상의 의미를 갖습니다.
                  최상의 품질을 위한 최선의 노력들이 모든 단계에서 이뤄집니다.
                  과연 스타벅스는 최상의 원두를 위해 어떤 노력을 하고 있을까요?
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="part2_wrap">
        <div className="part2">
          <div className="part2_title hidden">
            <h3>산지와 농장</h3>
          </div>
          <div className="part2_contents">
            <img src={arabicaList[2].path} alt="title" />
            <div className="part2_text">
              <h3>“고품질을 위한, 고지대를 향한”</h3>
              <p>
                고품질의 아라비카 원두는 고도가 높은 곳에서 재배됩니다. 큰
                일교차로 당도가 높고 밀도가 단단한 원두는 깊고 복합적인 풍미를
                냅니다.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="part3_wrap">
        <div className="part3">
          <div className="part3_title hidden">
            <h3>산지와 농장</h3>
          </div>
          <div className="part3_contents">
            <img src={arabicaList[3].path} alt="title" />
            <div className="part3_text">
              <h3>“커피 생산, 그 이상을 위한 끊임없는 노력”</h3>
              <p>
                스타벅스는 커피 농부들이 좋은 품질의 커피를 재배할 수 있도록
                그들을 돕고 있습니다. 전 세계 주요 커피 재배 지역에 10개의 농민
                지원 센터가 운영되고 있으며, 스타벅스 전문가들이 커피 경작을
                위한 연구를 하여 농부들에게 무료로 제공하고 있습니다.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="part4_wrap">
        <div className="part4">
          <div className="part4_title">
            <h3 className="hidden">산지와 농장</h3>
          </div>
          <div className="part4_contents">
            <img src={arabicaList[4].path} alt="title" />
            <div className="part4_text">
              <h3>“커피 농가와 지속적인 관계”</h3>
              <p>
                커피 농가의 미래가 곧 커피의 미래입니다. 이를 위해 스타벅스는
                커피 농부들과 공정한 커피 거래를 하며, 커피 농가와 지역사회가
                커피 산업으로 지속가능한 삶을 영위하도록 노력하고 있습니다.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="part5_wrap">
        <div className="part5">
          <div className="part5_title hidden">
            <h3>산지와 농장</h3>
          </div>
          <div className="part5_contents">
            <img src={arabicaList[5].path} alt="title" />
            <div className="part5_text">
              <h3>“까다롭고 엄격한 수확 과정”</h3>
              <p>
                스타벅스는 잘 익은 커피 열매만 수확합니다. 그리고 크기, 색깔 및
                밀도를 기준으로 최상의 열매를 한 번 더 골라냅니다. 보다 까다롭고
                엄격한 수확 과정이 곧 훌륭한 커피를 탄생시킵니다. 까다롭고
                엄격한 수확 과정을 거쳐야 비로소 최상의 커피 맛을 구현할 수
                있습니다.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="part6_wrap">
        <div className="part6">
          <div className="part6_title hidden">
            <h3>산지와 농장</h3>
          </div>
          <div className="part6_contents">
            <img src={arabicaList[6].path} alt="title" />
            <div className="part6_text">
              <h3>“고품질의 커피를 위한 수차례의 테이스트”</h3>
              <p>
                고품질 커피를 위한 스타벅스의 노력은 농부와 함께 생산하며
                수차례의 품질 체크를 통해 완성해 갑니다. 고품질의 커피 생산은
                농부에게 높은 가격으로 돌아가고, 고객들에게 풍성한 풍미의 커피로
                제공됩니다.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
