import "./Americano.scss";
import Section_Tag from "../../components/Section_Tag";
import React from "react";

export default function Espresso() {
  return (
    <div className="americano">
      <div className="americano_title">
        <Section_Tag />
        <h2>아메리카노</h2>
      </div>

      <div className="part1_wrap">
        <div className="part1_contents">
          <img src="../../image/americano/americano_title.jpg" alt="title" />
          <div className="part1_text">
            <h3>
              “아메리카노는 에스프레소의 진한 풍미를 잘 느낄 수 있는 음료입니다.
              ”
            </h3>
            <p>
              아메리카노는 에스프레소 샷 두 개를 추출하여 바로 컵에 붓고, 그
              위에 뜨거운 물을 재빠르게 부어 얇은 크레마 층이 형성되는
              음료입니다.
              <br />갓 분쇄된 에스프레소만이 깊은 풍미를 가진 아메리카노를 만들
              수 있습니다.
            </p>
          </div>
        </div>
      </div>

      <div className="part2_wrap">
        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/americano/americano_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>아이스 아메리카노</h4>
            <p>
              진한 에스프레소에 시원한 정수물과 얼음을 더하여 스타벅스의
              깔끔하고 강렬한 에스프레소를<br/> 가장 부드럽고 시원하게 즐길 수 있는
              커피
            </p>
          </div>
        </div>

        <div className="part2_contents">
          <div className="part2_img">
            <img src="../../image/americano/cafe_americano_thumb.jpg" alt="thumb" />
          </div>
          <div className="part2_text">
            <h4>카페 아메리카노</h4>
            <p>
            진한 에스프레소와 뜨거운 물을 섞어 스타벅스의 깔끔하고 강렬한 에스프레소를 가장 부드럽게 잘 느낄 수 있는 커피
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
